import React from "react";
import styled, { css } from "styled-components";
//import Container from "react-bootstrap/Container";

import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

import "./mapapp.css";

import { VectorMap } from "@south-paw/react-vector-maps";
import world from "../world";

//import SideBar from "./components/sidebar";

class MapApp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      country: "",
      confirmed: 0,
      deaths: 0,
      totaldeaths: 0,
      totalcases: 0
    };

    this.state = {
      totaldeaths: new Intl.NumberFormat("en", {
        style: "decimal"
      }).format(world.totalDeaths),
      totalcases: new Intl.NumberFormat("en", {
        style: "decimal"
      }).format(world.totalcases)
    };
    //
    // this.onInputChange = this.onInputChange.bind(this);

    //  this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.handler = this.handler.bind(this);
  }

  createCSS() {
    let styles = "";

    for (let i = 0; i < world.layers.length; i += 1) {
      styles +=
        `

           &[id=\"` +
        world.layers[i].id +
        `\"] {
             fill: ` +
        world.layers[i].color +
        `

         }
       `;
    }

    console.log(styles);

    return css`
      ${styles}
    `;
  }

  handler({ target }) {
    //console.log({ term });
    this.props.onCountryChange({ target });
  }

  render() {
    const Map = styled.div`
      margin: 1rem auto;
      width: 800px;
      object-fit: contain;
      align: center;

      svg {
        stroke: #fff;

        // All layers are just path elements
        path {
          fill: green;
          cursor: pointer;
          outline: none;

          // When a layer is hovered
          &:hover {
            fill: rgba(168, 43, 43, 0.83);
          }

          // When a layer is focused.
          &:focus {
            fill: rgba(168, 43, 43, 0.6);
          }
        }
      }
    `;

    const layerProps = {
      onMouseEnter: ({ target }) => {
        //console.log(this.props);
        //this.props.changeCountry({ target });
        this.handler({ target });
        this.setState({
          country: target.attributes.name.value,
          confirmed: new Intl.NumberFormat("en", {
            style: "decimal"
          }).format(target.attributes.confirmed.value),
          deaths: new Intl.NumberFormat("en", {
            style: "decimal"
          }).format(target.attributes.deaths.value)
        });
        //console.log(this.state.country);
      },
      onMouseLeave: ({ target }) =>
        this.setState({ country: "", confirmed: 0, deaths: 0 }),
      onClick: ({ target }) => console.log(target.attributes.name.value)
    };

    return (
      <div className="mapArea">
        <TransformWrapper>
          {({ zoomIn, zoomOut, resetTransform, ...rest }) => (
            <React.Fragment>
              <div className="tools" align="right">
                <button onClick={zoomIn}>+</button>
                <button onClick={zoomOut}>-</button>
                <button onClick={resetTransform}>x</button>
              </div>
              <TransformComponent>
                <Map>
                  <VectorMap
                    {...world}
                    // onMouseEnter={({ target }) => {
                    //   console.log("Div is clicked", target.attributes.name.value);
                    // }}
                    layerProps={layerProps}
                  />
                </Map>
              </TransformComponent>
            </React.Fragment>
          )}
        </TransformWrapper>
      </div>
    );
  }
}

export default MapApp;
