class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

    this.state = {
      dropdownOpen: false,
      radioSelected: 2,
      country: "",
      confirmed: 0,
      deaths: 0,
      totaldeaths: 0,
      totalcases: 0
    };
    // this.state = {
    //   totalDeaths: new Intl.NumberFormat("en", {
    //     style: "decimal"
    //   }).format(world.totalDeaths),
    //   totalCases: new Intl.NumberFormat("en", {
    //     style: "decimal"
    //   }).format(world.totalCases)
    // };
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  onRadioBtnClick(radioSelected) {
    this.setState({
      radioSelected: radioSelected
    });
  }

  loading = () => (
    <div className="animated fadeIn pt-1 text-center">Loading...</div>
  );

  changeCountry(term) {
    //console.log(term.term.target.attributes.name.value);
    this.setState({
      country: term.term.target.attributes.name.value,
      confirmed: new Intl.NumberFormat("en", {
        style: "decimal"
      }).format(term.term.target.attributes.confirmed.value),
      deaths: new Intl.NumberFormat("en", {
        style: "decimal"
      }).format(term.term.target.attributes.deaths.value)
    });
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <div className="Myheader">Coronavirus Disease 2020</div>
        </Row>
        <Row>
          <div className="countryName">Country: {this.state.country}</div>
        </Row>
        <Row align="right">
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-warning">
              <CardBody className="pb-0">
                <div className="text-value">{this.state.confirmed} </div>
                <div>Number of cases </div>
              </CardBody>
              <div className="chart-wrapper" style={{ height: "35px" }}></div>
            </Card>
          </Col>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-danger">
              <CardBody className="pb-0">
                <div className="text-value">{this.state.deaths}</div>
                <div> Deaths </div>
              </CardBody>
              <div
                className="chart-wrapper mx-3"
                style={{ height: "35px" }}
              ></div>
            </Card>
          </Col>
        </Row>
        <div align="right">
          <small>
            Data Source:{" "}
            <a href="https://github.com/CSSEGISandData/COVID-19">
              CSSEGISandData{" "}
            </a>{" "}
          </small>
        </div>
        <Row>
          <Col>
            <Card>
              <CardBody>
                <Row>
                  <Col sm="5">
                    <CardTitle className="mb-0"> World map</CardTitle>
                    <div className="small text-muted">2020</div>
                  </Col>
                  <Col sm="7" className="d-none d-sm-inline-block"></Col>
                </Row>
                <div align="center">
                  <MapApp
                    onCountryChange={term => this.changeCountry({ term })}
                  />
                </div>
              </CardBody>
            </Card>

            <Card>
              <CardBody>
                <Row>
                  <Col sm="5">
                    <CardTitle className="mb-0">
                      {" "}
                      Infection and Death curve
                    </CardTitle>
                    <div className="small text-muted">2020</div>
                  </Col>
                  <Col sm="7" className="d-none d-sm-inline-block"></Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

